import styled from "styled-components";

export const Container = styled.div`
  text-align: center;
  align-items: center;
  padding: 90px 150px;
`;

export const Title = styled.h1`
  font-size: 60px;
  font-weight: 700;
  margin-bottom: 30px;
  color: #444888;
  background: #ffffff;
`;

export const Text = styled.p`
  font-size: 20px;
  font-weight: 500;
  margin-bottom: 30px;

  color: #444888;
  background: #ffffff;
`;

export const Hero = styled.div`
  display: flex;
  justify-content: center;
`;
